/* button template */
#sendmail-testform form button, #cf7-smtp input[type=submit] {
  margin: 1rem 1rem 0 auto;
  display: flex;
}

/**
 * This stylesheet is used to style the settings page of the plugin.
 */
#cf7-smtp .integration-icon {
  display: inline-block;
  padding-block: inherit;
  margin: 0 0 0 0.7em;
  width: 30px;
}
#cf7-smtp .title {
  display: inline-block;
}
#cf7-smtp .infobox {
  display: inline-block;
}
#cf7-smtp div#cf7-smtp-auth label {
  display: block;
  margin-bottom: 10px;
}
#cf7-smtp select,
#cf7-smtp input[type=email],
#cf7-smtp input[type=text],
#cf7-smtp input[type=number] {
  width: 100%;
}
#cf7-smtp input#cf7_smtp_user_pass.unsafe {
  box-shadow: 0 0 0 1px #f44336 inset;
}
#cf7-smtp .tip.schedule {
  padding: 8px 16px;
  margin: 16px auto 0;
  display: flex;
  justify-content: center;
  background: #ffebeb;
  border: 1px solid #f44336;
  border-radius: 5px;
}
#cf7-smtp .tip.schedule * {
  margin: 0 8px;
  padding: 0;
}
#cf7-smtp .tip.schedule h1 {
  font-size: 30px;
}

#sendmail-testform form {
  clear: both;
}
#sendmail-testform form label,
#sendmail-testform form input,
#sendmail-testform form textarea {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 0.4rem;
}
#sendmail-testform pre {
  padding: 30px 20px;
  box-sizing: border-box;
  border-left: 3px solid #2f333a;
  transition: 1s ease border-left-color;
  background: #2f333a;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  gap: 8px;
}
#sendmail-testform pre.enabled {
  border-color: #3372b3;
}
#sendmail-testform pre.ok {
  border-color: #4caf50;
}
#sendmail-testform pre.error {
  border-color: #f44336;
}
#sendmail-testform pre code {
  white-space: break-spaces;
  font-size: 0.8rem;
  color: white;
  background: #2f333a;
}
#sendmail-testform pre span.timestamp {
  text-align: right;
  font-size: 11px;
  font-weight: bold;
  color: white;
  background: #3372b3;
  display: block;
  margin: 4px 0 4px auto;
  padding: 2px 8px;
  border-radius: 4px;
  line-height: 1;
}

.smtp-style-chart h4 {
  text-align: center;
}
.smtp-style-chart #pie-container {
  margin: auto;
  width: 250px;
}
.smtp-style-chart span.chart-icon {
  display: block;
  width: auto;
  text-align: center;
  font-size: 120px;
  line-height: 1;
}
.smtp-style-chart .no-chart-title {
  font-size: 1.3rem;
  font-style: italic;
  text-align: center;
  font-weight: lighter;
  opacity: 0.5;
}

/* The form test animation */
#sendmail-testform span.mail-init {
  animation: translateMail 15s 1;
  display: inline-block;
}

@keyframes translateMail {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(200px);
  }
}