#sendmail-testform {
  // the test form style
  form {
	clear: both;
	label,
	input,
	textarea {
	  display: flex;
	  justify-content: space-between;
	  width: 100%;
	  margin-bottom: .4rem;
	}
	button {
	  @extend %button
	}
  }

  // the test form output box
  pre {
	padding: 30px 20px;
	box-sizing: border-box;
	border-left: 3px solid $grayish_blue;
	transition: 1s ease border-left-color;
	background: $grayish_blue;
	border-radius: 8px;
	display: flex;
	flex-direction: column;
	overflow: hidden;
	gap: 8px;

	&.enabled {
	  border-color: $blue;
	}

	&.ok {
	  border-color: $green;
	}

	&.error {
	  border-color: $red;
	}

	code {
	  white-space: break-spaces;
	  font-size: .8rem;
	  color: white;
	  background: darken($grayish_blue, .10);
	}

	span.timestamp {
	  text-align: right;
	  font-size: 11px;
	  font-weight: bold;
	  color: white;
	  background: $blue;
	  display: block;
	  margin: 4px 0 4px auto;
	  padding: 2px 8px;
	  border-radius: 4px;
	  line-height: 1;
	}
  }
}
